import React, { FC, FormEvent, MutableRefObject, useRef } from "react";
import emailjs from "@emailjs/browser";
import JOBS from "../seeds/jobs.json";

const Contact: FC = (): JSX.Element => {
  const form = useRef<HTMLFormElement>() as MutableRefObject<HTMLFormElement>;

  const sendEmail = (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    emailjs.sendForm("service_8syqdsh", "template_zm3qwks", form.current, "Cu47hTG1cjwYiONdD")
      .then((result) => {
        alert("Gi thnh cng");
      }, (error) => {
        alert("Gi tht bi. nh dnh file khng c chp nhn");
      });
  };

  return (
    <div className="contact" id="contact">
      <div className="contact-form">
        <form className="form" ref={form} onSubmit={sendEmail}>
          <div className="form-item">
            <label>H tn:</label>
            <input type="text" name="full_name" required />
          </div>

          <div className="form-item">
            <label>S in thoi:</label>
            <input type="text" name="phone_number" required />
          </div>

          <div className="form-item">
            <label>V tr ng tuyn:</label>
            <select name="job_title" required>
              {JOBS.map((item, index) => <option value={item.job} key={index}>{item.job}</option>)}
            </select>
          </div>

          <div className="form-item">
            <label>H s ng tuyn:</label>
            <input type="file" name="H s ng tuyn" accept="application/msword|application/pdf" required />
          </div>

          <div className="form-item">
            <label>Tin nhn:</label>
            <textarea name="note" rows={3}></textarea>
          </div>

          <div className="form-action">
            <input type="submit" value="Gi" />
          </div>
        </form>
      </div>
    </div>
  );
};

export default Contact;