import React, { FC, useCallback, useState } from "react";
import Gallery from "react-photo-gallery";
import Carousel, { Modal, ModalGateway } from "react-images";

const IMAGES = [
  { src: "https://i.pinimg.com/564x/c6/e5/ae/c6e5ae27f0b034d4acc237b28070dbab.jpg", width: 2, height: 3 },
  { src: "https://i.pinimg.com/564x/08/d4/83/08d4834ff8d6577f9e79d87185578b25.jpg", width: 2, height: 3 },
  { src: "https://i.pinimg.com/564x/7a/d3/da/7ad3dae85dc1ac5ba5a4840321dd1344.jpg", width: 1, height: 1 }
];

const WhatWePursuit: FC = (): JSX.Element => {
  const [currentImage, setCurrentImage] = useState<number>(0);
  const [viewerIsOpen, setViewerIsOpen] = useState(false);

  const openLightbox = useCallback((event: any, { photo, index }: any) => {
    setCurrentImage(index);
    setViewerIsOpen(true);
  }, []);

  const closeLightbox = () => {
    setCurrentImage(0);
    setViewerIsOpen(false);
  };

  return (
    <div className="about-item">
      <div className="about-text">
        <div className="about-heading">What we pursuit?</div>
        <div className="about-content">
            Trong nhiu nm sinh ra v ln ln  Vit Nm chng ti vn lun cm thy b tc khi ngh v chuyn: Cui tun ny i u?. Loanh quanh trong thnh ph ch ch c i n, i xem phim, i shopping v nightlife. S mong mi c nhng loi hnh gii tr mi lun m nh chng ti qua nhiu nm v sau cng chng ti  bt tay vo xy dng mt ni nh th. Mt ni c nhng game v hot ng gii tr tht vui, khng gian tht chill v tri nghim tht tt. Mt ni y s phn khch m vn v cng vn minh, mt ni  n hng tun, mt ni tn l The Coolest.
        </div>
      </div>

      <Gallery photos={IMAGES} onClick={openLightbox} />
      {/*eslint-disable-next-line @typescript-eslint/ban-ts-comment*/}
      {/*@ts-ignore*/}
      <ModalGateway>
        {viewerIsOpen ? (
          <Modal onClose={closeLightbox}>
            <Carousel
              currentIndex={currentImage}
              views={IMAGES.map(x => ({ ...x, source: x.src }))}
            />
          </Modal>
        ) : null}
      </ModalGateway>
    </div>
  );
};

export default WhatWePursuit;